import React, {useState, useContext} from 'react'
import {AlertContext} from '../context/alert/alertContext'
import {FirebaseContext} from '../context/firebase/firebaseContext'

export const FormLom = () => {

  const [value, setValue] = useState('')
  const [valueTest,setValueType] = useState('')
  const [valueHeight,setValueHeiht] = useState('')
  const [valuePrice,setValuePrice] = useState('')
  const [valueDate,setValueDate] = useState(new Date())
  const alert = useContext(AlertContext)
  const firebase = useContext(FirebaseContext)


  const submitHandler = event => {
    event.preventDefault()

    if (value.trim() || valueTest) {
      firebase.addNote(value.trim(),valueTest,valueHeight).then(() => {
        alert.show('Успешно отправлено', 'success')
      }).catch(() => {
        alert.show('Что-то пошло не так', 'danger')
      })
      setValueType('')
      setValue('')
  
    } else {
      alert.show('Введите данные')
    }
  }


  return (
    <form>
      <div className="row">

      <div className="form-group w-25 pb-2 col">
        <input
          type="text"
          className="form-control"
          placeholder="Введите название металла"
          value={value}
          onChange={e => setValue(e.target.value)} />

      </div>

      <div className="form-group w-25 pb-2 col">
          <input
            type="text"
            className="form-control"
            placeholder="Введите вид лома"
            value={valueTest}
            onChange={e => setValueType(e.target.value)} />

        </div>
        
        <div className="form-group pb-2 col w-25">
        <button type="button" className="btn btn-primary ml-3" style={{height:38+ 'px'}} onClick={submitHandler}>Отправить</button>
        </div>
        
        </div>
    </form>
    
  )
}
